"use client";

import {
  createContext,
  useContext,
  useState,
  useCallback,
  ReactNode,
  useRef,
  useEffect,
} from "react";
import { useRouter } from "next/navigation";
import { authService } from "../Services/AuthServices";
import { ProfileService } from "../Services/ProfileServices";
import { PredictionService } from "../Services/PredictionsServices";
import { toast } from "sonner";
import { HoroscopeServices } from "../Services/HoroscopeServices";

interface AuthContextType {
  checkAuth: () => Promise<any>;
  login: (credentials: any) => Promise<any>;
  signUp: (formdata: any) => Promise<any>;
  handleGoogleLogin: (idToken: any) => Promise<any>;
  logout: () => Promise<any>;
  updateProfile: (formdata: any) => Promise<any>;
  GetPlanet: () => Promise<any>;
  Getnavam: () => Promise<any>;
  Getmahadasha: () => Promise<any>;
  getAntharDasha: () => Promise<any>;
  addProfile: (name: string, nickname: string) => Promise<any>;
  switchProfile: (profileId: string) => Promise<any>;
  getPredictions: () => Promise<any>;
  submitPredictionFeedback: (feedbackData: any) => Promise<any>;
  AstroLogic: () => Promise<void>;
  feedbackStatus: any;
  getAllFeedbackPredciton: () => Promise<any>;
  loading: boolean;
  isAuthenticated: boolean;
  user: any;
  errors: any;
  planet?: any;
  navam?: any;
  mahadaha?: any;
  anthardahsa?: any;
  predictions?: any;
  servaysStatus: any;
  astro: any;
  feedbackcompleteStatus: any;

  getJobsOptions: () => Promise<{ [key: string]: string[] }> | any;
  getEducationOptions: () => Promise<{ [key: string]: string[] }> | any;
  GetSurveyQuestions: () => Promise<any>;
  DeleteDeceasedMember: (id: any) => Promise<any>;
  GetSurveyStatus: () => Promise<any>;
  saveDeceasedFamily: (members: any) => Promise<any>;
  GetDeceasedFamily: () => Promise<any>;
  SaveSurveyAnswers: (answers: any) => Promise<any>;
  GetUserSurveyAnswers: () => Promise<any>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const router = useRouter();
  const [loading, setIsLoading] = useState(true);

  // ========================== AUTH STATES ==========================
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
  const [user, setUser] = useState<any>(null);
  const [errors, setErrors] = useState({});

  // ========================== HOROSCOPE STATE ==========================
  const [planet, setPlanet] = useState<any>(null);
  const [navam, setnavam] = useState<any>(null);
  const [mahadaha, setmahadaha] = useState<any>(null);
  const [anthardahsa, Setanthardahsa] = useState<any>(null);
  const [astro, setastro] = useState<any>(null);

  // ========================== PREDICTIONS STATE ==========================
  const [predictions, setPredictions] = useState<any>(null);
  const [feedbackStatus, setFeedbackStatus] = useState<any>(null);
  const [feedbackcompleteStatus, setcompleteFeedbackStatus] =
    useState<any>(null);

  // ========================= SERVAYS STATE ===============================
  const [servaysStatus, setservaysStatus] = useState<any>(null);

  // ========================== DEBOUNCE LOGIC ==========================
  const isCheckingRef = useRef(false);
  const lastCheckTimeRef = useRef(0);
  const DEBOUNCE_TIME = 500;

  // ============================ CHECK AUTH =================
  const checkAuth = useCallback(async () => {
    const now = Date.now();

    if (isCheckingRef.current) {
      console.log("Auth check already in progress, skipping...");
      return;
    }

    if (now - lastCheckTimeRef.current < DEBOUNCE_TIME) {
      console.log("Auth check debounced");
      return;
    }

    isCheckingRef.current = true;
    lastCheckTimeRef.current = now;
    setIsLoading(true);

    try {
      const result = await authService.verifyAuth();
      if (result.success && result.user) {
        await GetSurveyStatus();
        await GetFeedbackStatus();
        setUser(result.user);
        setIsAuthenticated(true);
      } else {
        setUser(null);
        setIsAuthenticated(false);
      }
    } catch (err) {
      setUser(null);
      setIsAuthenticated(false);
    } finally {
      setIsLoading(false);
      isCheckingRef.current = false;
    }
  }, []);

  // ============ CHECK AUTH ON MOUNT (Page Refresh) ============
  useEffect(() => {
    checkAuth();
  }, [checkAuth]);

  // ============================ Login ======================
  const login = async (credentials: { email: string; password: string }) => {
    setIsLoading(true);
    try {
      await authService.login(credentials);
      await checkAuth();
      toast.success("Login Successful!");
      router.push("/");
    } catch (err: any) {
      const message = err.message || "Login failed. Please try again.";
      toast.error(message);
      throw err;
    } finally {
      setIsLoading(false);
    }
  };

  // ========================== SIGN UP ======================
  const signUp = async (formdata: any) => {
    setIsLoading(true);
    try {
      await authService.register(formdata);
      toast.success("Account Created Successfully!");
    } catch (error: any) {
      if (error.errors && typeof error.errors === "object") {
        setErrors(error.errors);
      } else {
        const message = error.message || "Registration failed";
        toast.error(message);
      }
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  // ===================== GOOGLE LOGIN =====================
  const handleGoogleLogin = async (idToken: string) => {
    setIsLoading(true);
    setErrors({});

    try {
      const response: any = await authService.googleLogin(idToken);
      setUser(response?.user);
      await checkAuth();
      toast.success("Login Successful!");
    } catch (error: any) {
      const errorMessage = error?.message || "Google login failed";
      toast.error(errorMessage);
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  // ===================== Logout ================================
  const logout = async () => {
    try {
      await authService.logout();
      setUser(null);
      setIsAuthenticated(false);
      setPredictions(null);
      toast.info("Logged out successfully");
      router.push("/auth");
    } catch (err) {
      toast.error("Logout failed");
    }
  };

  // =================== UPDATE PROFILE =================================
  const updateProfile = async (formData: any) => {
    setIsLoading(true);
    try {
      await ProfileService.update(formData);
      await checkAuth();
      toast.success("Profile updated successfully");
    } catch (error: any) {
      const message = error.message || "Profile update failed";
      toast.error(message);
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  // ======================== ADD PROFILE ===========================
  const addProfile = useCallback(
    async (name: string, nickname: string) => {
      if (!name || !nickname) {
        toast.error("Name and nickname are required");
        return;
      }

      setIsLoading(true);
      try {
        const result: any = await ProfileService.addProfile(name, nickname);

        if (result.success) {
          await checkAuth();
          toast.success("Profile added successfully");
        } else {
          throw new Error(result.error || "Failed to add profile");
        }
      } catch (error: any) {
        console.error("Add profile error:", error);
        toast.error(error.message || "Failed to add profile");
        throw error;
      } finally {
        setIsLoading(false);
      }
    },
    [checkAuth]
  );

  // ======================== SWITCH PROFILE ========================
  const switchProfile = useCallback(
    async (profileId: string) => {
      if (!profileId) {
        toast.error("Invalid profile ID");
        return;
      }

      setIsLoading(true);
      try {
        const result: any = await ProfileService.switchProfile(profileId);
        if (result.success) {
          await checkAuth();
          setPredictions(null);
          toast.success("Profile switched successfully");
        } else {
          throw new Error(result.error || "Failed to switch profile");
        }
      } catch (error: any) {
        console.error("Profile switch error:", error);
        toast.error(error.message || "Failed to switch profile");
        throw error;
      } finally {
        setIsLoading(false);
      }
    },
    [checkAuth]
  );

  // ======================== PLANET HOUSE ==========================
  const GetPlanet = async () => {
    try {
      const result: any = await HoroscopeServices.getPlanet();
      if (result.success) {
        setPlanet(result.data);
      } else {
        setPlanet(null);
      }
    } catch (error) {
      setPlanet(null);
      throw error;
    }
  };

  // ======================== NAVAMSHAKA ==========================
  const Getnavam = async () => {
    try {
      const result: any = await HoroscopeServices.getNavam();
      if (result.success) {
        setnavam(result.data);
      } else {
        setnavam(null);
      }
    } catch (error) {
      setnavam(null);
      throw error;
    }
  };

  // ========================= Mahadasha ==========================
  const Getmahadasha = async () => {
    try {
      const result: any = await HoroscopeServices.getMahadasha();
      if (result.success) {
        setmahadaha(result.data);
      } else {
        setmahadaha(null);
      }
    } catch (error) {
      setmahadaha(null);
      throw error;
    }
  };

  // ========================= ANTHARDASHA ==========================
  const getAntharDasha = async () => {
    try {
      const result: any = await HoroscopeServices.getAnthardasha();
      if (result.success) {
        Setanthardahsa(result.data);
      } else {
        Setanthardahsa(null);
      }
    } catch (error) {
      Setanthardahsa(null);
      throw error;
    }
  };

  // ======================== GET PREDICTIONS ==========================
  const getPredictions = useCallback(async () => {
    try {
      const planets = [
        "Sun",
        "Moon",
        "Mars",
        "Mercury",
        "Jupiter",
        "Venus",
        "Saturn",
        "Rahu",
        "Ketu",
      ];

      const results = await Promise.allSettled(
        planets.map((planet) => PredictionService.getPlanetPrediction(planet))
      );

      const predictionsData: any = {};

      results.forEach((result, index) => {
        if (result.status === "fulfilled" && result.value.success) {
          predictionsData[planets[index]] = result.value.data;
        }
      });

      setPredictions(predictionsData);
      return { success: true, data: predictionsData };
    } catch (error) {
      console.error("Error loading predictions:", error);
      setPredictions(null);
      throw error;
    }
  }, []);

  // ======================== SUBMIT PREDICTION FEEDBACK ==========================
  const submitPredictionFeedback = useCallback(async (feedbackData: any) => {
    try {
      const result = await PredictionService.submitFeedback(feedbackData);
      if (result.success) {
        await getAllFeedbackPredciton();
        await GetFeedbackStatus();
        return result;
      } else {
        throw new Error(result.error || "Failed to submit feedback");
      }
    } catch (error: any) {
      console.error("Submit feedback error:", error);
      throw error;
    }
  }, []);

  // ================================= PREDICTION FEEDBACK GET =====================
  const getAllFeedbackPredciton = useCallback(async () => {
    try {
      const result = await PredictionService.getUserFeedbacks();
      if (result.success) {
        setFeedbackStatus(result.data);
      } else {
        throw new Error(result.error || "Failed to get feedbacks");
      }
    } catch (error: any) {
      console.error("Submit feedback error:", error);
      throw error;
    }
  }, [submitPredictionFeedback]);

  // ================================ FEEDBACK =============================
  const GetFeedbackStatus = async () => {
    try {
      const result: any = await authService.getFeedbackStatus();
      if (result.success) {
        return setcompleteFeedbackStatus(result.data);
      }
    } catch (error) {
      console.error("GetFeedbackStatus error:", error);
    }
  };

  // ============================  SERWAYS =============================
  const GetSurveyQuestions = async () => {
    try {
      const result: any = await authService.getSurveyQuestions();
      if (result.success) {
        return result.data;
      }
    } catch (error) {
      console.error("GetSurveyQuestions error", error);
    }
  };

  const GetUserSurveyAnswers = async () => {
    try {
      const result: any = await authService.getUserSurveyAnswers();
      if (result.success) {
        return result.data;
      }
    } catch (error) {
      console.error("GetUserSurveyAnswers error", error);
    }
  };

  const SaveSurveyAnswers = async (answers: any) => {
    try {
      const result: any = await authService.saveSurveyAnswers(answers);
      if (result.success) {
        toast.success("Answers saved successfully");
        return result.data;
      }
    } catch (error) {
      console.error("SaveSurveyAnswers error", error);
    }
  };

  const GetDeceasedFamily = async () => {
    try {
      const result: any = await authService.getDeceasedFamily();
      if (result.success) {
        return result.data;
      }
    } catch (error) {
      console.error("GetDeceasedFamily error", error);
    }
  };

  const saveDeceasedFamily = async (members: any) => {
    try {
      const result: any = await authService.saveDeceasedFamily(members);
      if (result.success) {
        return result.data;
      }
    } catch (error) {
      console.error("SaveDeceasedFamily error", error);
    }
  };

  const GetSurveyStatus = async () => {
    try {
      const result: any = await authService.getSurveyStatus();
      if (result.success) {
        return setservaysStatus(result.data);
      }
    } catch (error) {
      console.error("GetSurveyStatus error", error);
    }
  };

  const DeleteDeceasedMember = async (id: number) => {
    try {
      const result: any = await authService.deleteDeceasedMember(id);
      if (result.success) {
        return result.data;
      }
    } catch (error) {
      console.error("DeleteDeceasedMember error", error);
    }
  };

  const getJobsOptions = async () => {
    try {
      const result: any = await authService.getJobsOptions();
      if (result.success) {
        return result.data;
      }
      throw new Error(result.error);
    } catch (error) {
      throw error;
    }
  };

  const getEducationOptions = async () => {
    try {
      const result: any = await authService.getEducationOptions();
      if (result.success) {
        return result.data;
      }
      throw new Error(result.error);
    } catch (error) {
      throw error;
    }
  };

  // =========================== astro Data =========================
  const AstroLogic = async () => {
    try {
      const result: any = await HoroscopeServices.astrologic();
      if (result.success) {
        setastro(result.data.result.data);
      } else {
        setastro(null);
      }
    } catch (error) {
      setastro(null);
      throw error;
    }
  };

  return (
    <AuthContext.Provider
      value={{
        user,
        loading,
        isAuthenticated,
        errors,
        login,
        signUp,
        handleGoogleLogin,
        checkAuth,
        logout,
        addProfile,
        switchProfile,
        updateProfile,
        GetPlanet,
        planet,
        Getnavam,
        navam,
        Getmahadasha,
        mahadaha,
        getAntharDasha,
        anthardahsa,
        predictions,
        getPredictions,
        submitPredictionFeedback,
        feedbackStatus,
        getAllFeedbackPredciton,
        GetSurveyQuestions,
        DeleteDeceasedMember,
        GetSurveyStatus,
        saveDeceasedFamily,
        GetDeceasedFamily,
        SaveSurveyAnswers,
        GetUserSurveyAnswers,
        getJobsOptions,
        getEducationOptions,
        servaysStatus,
        AstroLogic,
        astro,
        feedbackcompleteStatus,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth must be used within AuthProvider");
  }
  return context;
};
