import { apiClient } from "../lib/apiClient";
import { toast } from "sonner";


export const PredictionService = {

    getPlanetPrediction: async (planetName: string) => {
        try {
            const response = await apiClient.get(`/api/prediction/${planetName.toLowerCase()}`);
            return { success: true, data: response };
        } catch (error: any) {
            console.error(`Failed to get ${planetName} prediction:`, error);
            return { success: false, error: error.message };
        }
    },


    submitFeedback: async (feedbackData: {
        planet: string;
        predictionText: string;
        category: string;
        feedback: "correct" | "incorrect" | "undecided";
        language: string;
    }) => {
        try {
            const response = await apiClient.post("/api/prediction/feedback", feedbackData);
            toast.success("Feedback submitted successfully");
            return { success: true, data: response };
        } catch (error: any) {
            console.error("Failed to submit feedback:", error);
            toast.error("Failed to submit feedback");
            return { success: false, error: error.message };
        }
    },


    getUserFeedbacks: async () => {
        try {
            const response:any = await apiClient.get("/api/feedback");
            return { success: true, data: response.data };
        } catch (error: any) {
            console.error("Failed to get feedbacks:", error);
            return { success: false, error: error.message };
        }
    },
};
