import { apiClient } from '../lib/apiClient';
import { toast } from 'sonner';

export const authService = {
    // ========================================== Verify user authentication ============================================
    verifyAuth: async () => {
        try {
            const endpoint = '/api/profile';
            const user = await apiClient.get(endpoint);
            return { success: true, user };
        } catch (error: any) {
            return { success: false, error: error.message };
        }
    },


    // ================================================= User login ====================================================
    login: async (credentials: any) => {
        return await apiClient.post('/api/auth/login', credentials);
    },

    // =============================================== User registration ===============================================
    register: async (userData: any) => {

        return await apiClient.post('/api/auth/register', userData);
    },

    // ==================================================== Google login ===============================================
    googleLogin: async (idToken: string) => {

        return await apiClient.post('/api/auth/google-login', { idToken });
    },

    // =========================================================== Forgot password =====================================
    forgotPassword: async (email: string) => {
        return await apiClient.post('/api/users/forgot-password', { email });
    },

    // ============================================================ logout =============================================
    logout: async () => {
        return await apiClient.post('/api/auth/logout');
    },


    getJobsOptions: async () => {
        try {
            const options = await apiClient.get('/api/jobs/options');
            return { success: true, data: options };
        } catch (error) {
            return error;
        }
    },

    getEducationOptions: async () => {
        try {
            const options = await apiClient.get('/api/education/options');
            return { success: true, data: options };
        } catch (error) {
            return error;
        }
    },

    getSubscriptionPackages: async () => {
        try {
            const packages = await apiClient.get('/api/subscription/packages');
            return { success: true, data: packages };
        } catch (error) {
            return error;
        }
    },

    getCurrentSubscription: async () => {
        try {
            const subscription = await apiClient.get('/api/subscription/current');
            return { success: true, data: subscription };
        } catch (error) {
            return error;
        }
    },

    initiateSubscriptionPayment: async (packageId: number, duration: 'monthly' | 'yearly') => {
        try {
            const result: any = await apiClient.post('/api/subscription/initiate', { packageId, duration });
            return { success: true, data: result };
        } catch (error) {
            return error;
        }
    },

    getFeedbackStatus: async () => {
        try {
            const result: any = await apiClient.get('/api/feedback/status');
            return { success: true, data: result.data };
        } catch (error) {
            return error;
        }
    },

    getSurveyQuestions: async () => {
        try {
            const result = await apiClient.get('/api/survey/questions');
            return { success: true, data: result };
        } catch (error) {
            return error;
        }
    },

    getUserSurveyAnswers: async () => {
        try {
            const result = await apiClient.get('/api/survey/answers');
            return { success: true, data: result };
        } catch (error) {
            return error;
        }
    },

    saveSurveyAnswers: async (answers: any) => {
        try {
            const result = await apiClient.post('/api/survey/answers', { answers });
            toast.success('Survey answers saved successfully');
            return { success: true, data: result };
        } catch (error) {
            return error;
        }
    },

    getDeceasedFamily: async () => {
        try {
            const result = await apiClient.get('/api/survey/deceased-family');
            return { success: true, data: result };
        } catch (error) {
            return error;
        }
    },

    saveDeceasedFamily: async (members: any[]) => {
        try {
            const result = await apiClient.post('/api/survey/deceased-family', { members });
            toast.success('Family information saved successfully');
            return { success: true, data: result };
        } catch (error) {
            return error;
        }
    },

    getSurveyStatus: async () => {
        try {
            const result:any = await apiClient.get('/api/survey/statistics');
            return { success: true, data: result.data };
        } catch (error) {
            return error;
        }
    },

    deleteDeceasedMember: async (id: number) => {
        try {
            const result = await apiClient.delete(`/api/survey/deceased-family/${id}`);
            toast.success('Member deleted successfully');
            return { success: true, data: result };
        } catch (error) {
            return error;
        }
    },
};

export type AuthService = typeof authService;