import { toast } from "sonner";
import { apiClient } from "../lib/apiClient";

export const ProfileService = {
  // ============================================================= Update user profile =============================================

  update: async (userData: any) => {
    try {
      const response = await apiClient.put("/api/profile", userData);
      return { success: true, data: response };
    } catch (error: any) {
      return { success: false, error: error.message };
    }
  },


  switchProfile: async (profileId: string) => {
    try {
      const response: any = await apiClient.post('/api/profile/switch', { profileId });
      toast.success('Profile switched successfully');
      return { success: true, data: response };
    } catch (error) {
      return error;
    }
  },

  addProfile: async (name: string, nickname: string) => {
    try {
      const response = await apiClient.post('/api/addProfile', { name, nikname: nickname });
      toast.success('Profile added successfully');
      return { success: true, data: response };
    } catch (error) {
      return error;
    }
  },


};


